<template>
  <div id="app">
	  <header-nav></header-nav>
    <div class="content_wrapper clearfix">
    	<router-view/>
    </div>
		<footer-nav></footer-nav> 
  </div>
</template>

<script>
	import headerNav from '@/components/headerNav'
	import footerNav from '@/components/footerNav'
	export default {
	  name: 'App',
	  components:{
	  	'header-nav':headerNav,
	  	'footer-nav':footerNav
	  }
	}

</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  /* 清除页面留白*/
  color: #2c3e50;
  padding:0px; 
  margin:0px; 
  position:absolute; 
  top:0px; 
  left:0px; 
  width:100%; 
  height:100%; 
  border:hidden;
}
.content_wrapper{
	min-height: 80%;
}
.clearfix::after{
		display: block;
    content: " ";
    height: 0;
    clear: both;
    visibility: hidden;
}
</style>



