<template>
	<div class="articles_wrapper">
		<el-card class="box-card">
		  <div slot="header" class="clearfix">
		    <h3>文章</h3>
		  </div>
		  <div v-for="info in articlesData" :key="info.title" class="text item" >
		  	<a href="#">
			  	<div>
			  		<h2>{{'标题： ' + info.title }}</h2>
			  	</div>
			    <div class="imgBox">
			    	<img :src="info.img" class="img" >
			    </div>
			    <div>
			  		<h4>{{'摘要 ：' + info.sum }}</h4>
			  	</div>
		  	</a>
		  </div>
		  
		</el-card>
		<!---->
        <div class="sider">
        	<div class="calendar_wrapper">
        		<el-calendar v-model="today">
				</el-calendar>
        	</div>
        	
        </div>
        
	</div>
</template>

<script>
	import articlesData from '@/lib/articlesData.js'

	export default {
		 components: {},
      data(){
      	return {
      		articlesData:articlesData,
      		today: new Date()
      	}
      }
    }
</script>

<style>

	
	.articles_wrapper{
		width: 100%;
		height: 100%;
		overflow: hidden;
		clear: both;
		display: flex;
	}
	 .text {
	 	width: 90%;
	 	min-height: 550px;
	 	padding-top: 3px;
	 	margin: 0 auto 100px auto;
	 	border-bottom: 1px solid gray;
	    font-size: 14px;
	    border-bottom: 1px solid gray;	
	  }
	  .text > a *{color: black;} 
	

	  
	
	  .clearfix:before,
	  .clearfix:after {
	    display: table;
	    content: "";
	  }
	  .clearfix:after {
	    clear: both
	  }
	  .imgBox{
	  	width: 70%;
	  	height: 400px;
	  	margin: 0 auto;
	  	
	  }
	  .img{
	  	width: 100%;
	  	height: 100%;
	  		
	  }
	  
	  .box-card {
	    width: 72%;
	    flex: 5;
	  }
	  .sider{
	  	flex: 2;
	  }
	  
	  .el-calendar {
	  	margin-top: 50px ;
	  }
	  .el-calendar-day{
	  	max-height: 3rem;
	  }
	  .is-today{
	  	background-color: #E1FFFF !important;
	  }

</style>


