<template>
<div id="menu">
	<el-menu
		
	  :default-active="activeIndex2"
	  class="el-menu-demo"
	  mode="horizontal"
	  @select="handleSelect"
	  background-color="#545c64"
	  text-color="#fff"
	  active-text-color="#ffd04b">
	  <el-menu-item index="1"><router-link to="/home" target="_self"><div>首页</div></router-link></el-menu-item>
	  <el-menu-item index="2"><router-link to="/articles" target="_self"><div>文章</div></router-link></el-menu-item>
	  <el-menu-item index="3"><router-link to="/contact" target="_self"><div>联系</div></router-link></el-menu-item>
	</el-menu>
</div>
</template>

<script>
	
  export default {
    data() {
      return {
        activeIndex: '1',
        activeIndex2: '1'
      };
    },
    methods: {
      handleSelect(key, keyPath) {
        console.log(key, keyPath);
      }
    },
    
  }
</script>

<style>
	*{
		text-decoration: none;
	}
	.el-menu{
		height: 60px;
		padding-left: 100px;
	}
	.el-menu-item{
		font-size: 15px;
	}
	
</style>


